.loading {
  position: fixed;
  top: 1rem;
  right: 1rem;
}

.flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 2rem auto;
  padding: 0.5rem;
}

.title {
  background: rgb(34, 193, 195);
  background: linear-gradient(
    0deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(62, 94, 122, 1) 100%
  );
}
